import { Component, OnInit, Input } from '@angular/core';
@Component({
  selector: 'dlv-mat-error-alert',
  template: `
    <ul class="floating-error" [ngClass]="{'hidden': !errorMsg, 'active' : errorMsg}">
        <li *ngFor="let error of errorMsg">{{error}}</li>
    </ul>
  `,
  styles: [`
    .hidden{display:none !important}.floating-error.hidden{animation:slideUp .5s ease-in}.floating-error.active{animation:slideDown 0.5s forwards}.floating-error{position:fixed;top:-1000px;min-width:120px;left:calc(50% - 10%);background:#bd362f;color:#fff;padding:10px 30px;z-index:999999}.floating-error p{margin:0}@keyframes slideDown{100%{top:10px}}@keyframes slideUp{0%{top:10px}100%{top:-1000px}}
  `]
})
export class DlvMatErrorComponent implements OnInit {

    @Input() errorMsg: Array<any>;
    
    constructor() { }

    ngOnInit() {
        setTimeout(() => {
            this.errorMsg.splice(0,this.errorMsg.length);
        }, 3000);
    }
}